import React, {Component} from 'react';

import './SVGGrid.scss';

import {COLORS} from '../../../../variables';
import Button from "../../../Controls/Button/Button";
import InputNumber from "../../../Controls/Input/InputNumber";
import Select from "../../../Controls/Select/Select";
import AppContext from "../../../../App-context";

import {isInViewport} from "../../../../variables";

let Snap;
let layout = {
	breakpoints: [{
		id: 0,
		label: "768px",
		value: 768
	}, {
		id: 1,
		label: "1024px",
		value: 1024
	}, {
		id: 2,
		label: "1280px",
		value: 1280
	}, {
		id: 3,
		label: "1920px",
		value: 1920
	}],	//Screen Width
	gridMin: +4,
	gridMax: +20,
	gridSize: +10,
	elements: [],
	screenWidth: 0
};

try {
	Snap = require('snapsvg-cjs');
} catch (e) {
	console.log(e)
}

class SVGGrid extends Component {

	buttons = {
		clear: {
			callback: () => {
				Snap(this.svgDiv).clear();
				layout.elements.length = 0;
				this.svgRender();
			},
			label: "Clear",
			style: {
				border: true,
				colorPrimary: COLORS.$grayDark,
				colorSecondary: COLORS.$grayLight
			}
		},
		content: {
			callback: () => {
				this.element = "content";
				this.drawElement(this.svgDiv, this.buttons.content);
			},
			label: "Content",
			type: "grid-content",
			style: {
				border: true,
				colorPrimary: COLORS.$colorSecondary_1_4,
				colorSecondary: COLORS.$colorSecondary_1_2
			}
		},
		export: {
			callback: () => {
				this.createPage();
			},
			label: "Open Layout in new tab",
			style: {
				border: false,
				colorPrimary: COLORS.$colorSecondary_2_3,
				colorSecondary: COLORS.$colorSecondary_2_1
			}
		},
		header: {
			callback: () => {
				this.element = "header";
				this.drawElement(this.svgDiv, this.buttons.header);
			},
			label: "Header",
			type: "grid-header",
			style: {
				border: true,
				colorPrimary: COLORS.$colorSecondary_2_4,
				colorSecondary: COLORS.$colorSecondary_2_2
			}
		},
		sidebar: {
			callback: () => {
				this.element = "sidebar";
				this.drawElement(this.svgDiv, this.buttons.sidebar);
			},
			label: "Sidebar",
			type: "grid-sidebar",
			style: {
				border: true,
				colorPrimary: COLORS.$colorPrimary_4,
				colorSecondary: COLORS.$colorPrimary_2
			}
		}
	};
	input = {
		callback: val => {
			this.setGridSize(val)
		},
		style: {
			border: true,
			colorPrimary: COLORS.$grayDark,
			colorSecondary: COLORS.$grayLight
		},
		attributes: {
			min: layout.gridMin,
			max: layout.gridMax,
			placeholder: "Select Grid Size",
			type: "number"
		}
	};
	select = {
		callback: val => {
			layout.screenWidth = val;
		},
		style: {
			border: true,
			colorPrimary: COLORS.$grayDark,
			colorSecondary: COLORS.$grayLight
		},
		options: layout.breakpoints
	};
	element;

	constructor() {
		super();
		this.state = {
			gridSize: layout.gridSize
		};
	}

	setGridSize = gridSize => {
		if (gridSize) {
			this.setState({gridSize});
		}
	};

	createPage() {
		function createRule(sheet, selector, styleString) {
			const rule = `${selector}  ${styleString}`;
			const index = sheet.cssRules.length; // insert at the end
			sheet.insertRule(rule, index);
		}

		if (layout.elements.length) {
			let newWindow = window.open("", "MsgWindow"),
				newDoc = newWindow.document,
				sheetMain = newDoc.head.appendChild(
					newDoc.createElement('style')
				).sheet,
				container = newDoc.querySelector("body"),
				wrapper = newDoc.createElement("DIV");

			wrapper.classList.add("grid-wrapper");

			createRule(sheetMain, `.grid-wrapper`, `{display: grid; height: 100%; width: 100%; grid-template-columns: repeat(${layout.gridSize}, 1fr); grid-template-rows: repeat(${layout.gridSize}, 1fr);}`);
			createRule(sheetMain, `.element-container`, `{box-sizing: border-box; padding: 5px; height: 100%; width: 100%;}`);
			createRule(sheetMain, `.grid-header`, `{background: ${COLORS.$colorSecondary_2_4}; height: 100%; width: 100%;}`);
			createRule(sheetMain, `.grid-sidebar`, `{background: ${COLORS.$colorPrimary_4}; height: 100%; width: 100%;}`);
			createRule(sheetMain, `.grid-content`, `{background: ${COLORS.$colorSecondary_1_4}; height: 100%; width: 100%;}`);
			createRule(sheetMain, `.grid-content`, `{background: ${COLORS.$colorSecondary_1_4}; height: 100%; width: 100%;}`);

			//Iterate over all possible breakpoints
			layout.breakpoints.forEach(breakpoint => {

				//Group by breakpoint
				let groupedArr = layout.elements.filter(element => element.screenWidth === breakpoint.value);

				if (groupedArr.length) {
					let style = newDoc.createElement('style'),
						sheetMediaQuery;

					style.setAttribute("media", `only screen and (min-width: ${breakpoint.value}px)`);
					sheetMediaQuery = newDoc.head.appendChild(
						style
					).sheet;

					groupedArr.map(element => {
						let gridElement = newDoc.createElement("DIV"),
							gridElementContainer = newDoc.createElement("DIV"),
							styleString = JSON.stringify(element.position, null, 1)
								.replace(/"/g, '')
								.replace(/,\n/g, ';')
								.replace(/\}/g, ';}');
						Object.assign(gridElement, element.attributes);
						gridElement.classList.add("element-container");
						gridElementContainer.classList.add(element.type);

						createRule(sheetMediaQuery, `#${element.attributes.id}`, styleString);

						gridElement.appendChild(gridElementContainer);
						wrapper.appendChild(gridElement)
					});
				}
			});
			container.appendChild(wrapper);
		}
	}

	svgRender() {
		//let snap = Snap(this.svgDiv);
		let {height, width} = window.getComputedStyle(this.svgDiv, null),
			snap = Snap(this.svgDiv),
			pattern,
			patternDim,
			path;

		//ToDo read from array and draw saved elements when grid size not changed
		//ToDo remove state change after drawing (scrolling und currentElement (header, content is saved in state)
		//ToDo update instead of deleting
		snap.clear();

		height = height.replace("px", "");
		width = width.replace("px", "");

		patternDim = {
			height: height / this.state.gridSize,
			width: width / this.state.gridSize
		};

		path = `M0,0H${patternDim.width}V${patternDim.height}H0V0`;
		pattern = snap.path(path).attr({
			fill: "none",
			stroke: COLORS.$grayMedium,
			strokeWidth: .5
		}).pattern(0, 0, patternDim.width, patternDim.height);

		snap.attr({
			viewBox: "0,0," + width + "," + height,
			fill: pattern
		});

		snap.rect(0, 0, width, height).attr({
			fill: pattern
		});
	}

	drawElement(svgNode, element) {
		let snap = Snap(svgNode),
			svgDiv = this.svgDiv,
			gridSize = this.state.gridSize,
			{height, width} = window.getComputedStyle(svgDiv, null),
			{colorPrimary, colorSecondary} = element.style,

			x_start,
			x_end,
			y_start,
			y_end,
			circleArr = [],
			polygon,
			polygonPos = {
				nw: {
					x: null,
					y: null
				},
				ne: {
					x: null,
					y: null
				},
				se: {
					x: null,
					y: null
				},
				sw: {
					x: null,
					y: null
				}
			},
			handler;

		height = height.replace("px", "");
		width = width.replace("px", "");
		height = height / gridSize;
		width = width / gridSize;


		function snapToGrid() {
			x_start = Snap.snapTo(width, x_start, width / 2 + 1);
			x_end = Snap.snapTo(width, x_end, width / 2 + 1);
			y_start = Snap.snapTo(height, y_start, height / 2 + 1);
			y_end = Snap.snapTo(height, y_end, height / 2 + 1);
		}

		function drag_Start() {
			svgNode.style.cursor = "move";
		}

		function drag_Move(dx, dy) {
			dx = +Snap.snapTo(width, dx, width / 2 + 1);
			dy = +Snap.snapTo(height, dy, height / 2 + 1);

			polygon.attr({
				"points":
					[
						polygonPos.nw.x + dx, polygonPos.nw.y + dy,
						polygonPos.ne.x + dx, polygonPos.ne.y + dy,
						polygonPos.se.x + dx, polygonPos.se.y + dy,
						polygonPos.sw.x + dx, polygonPos.sw.y + dy
					],
				fill: colorPrimary,
				stroke: colorSecondary,
				"stroke-dasharray": "10,10",
				strokeWidth: 1,
				opacity: .5
			});


			circleArr[0].attr({
				cx: polygonPos.nw.x + dx,
				cy: polygonPos.nw.y + dy
			});
			circleArr[1].attr({
				cx: polygonPos.ne.x + dx,
				cy: polygonPos.ne.y + dy
			});
			circleArr[2].attr({
				cx: polygonPos.se.x + dx,
				cy: polygonPos.se.y + dy
			});
			circleArr[3].attr({
				cx: polygonPos.sw.x + dx,
				cy: polygonPos.sw.y + dy
			});
		}

		function drag_Stop() {

			//Save Position
			polygonPos.nw.x = +polygon.attr("points")[0];
			polygonPos.nw.y = +polygon.attr("points")[1];
			polygonPos.ne.x = +polygon.attr("points")[2];
			polygonPos.ne.y = +polygon.attr("points")[3];
			polygonPos.se.x = +polygon.attr("points")[4];
			polygonPos.se.y = +polygon.attr("points")[5];
			polygonPos.sw.x = +polygon.attr("points")[6];
			polygonPos.sw.y = +polygon.attr("points")[7];

			polygon.attr({
				opacity: 1,
				"stroke-dasharray": "none",
			});

			svgNode.style.cursor = "default";
		}

		function resize_Start(x, y, e) {
			handler = e.currentTarget;
		}

		function resize_Move(dx, dy, x, y, e) {
			let pos = handler.dataset.pos;

			dx = +Snap.snapTo(width, dx, width / 2 + 1);
			dy = +Snap.snapTo(height, dy, height / 2 + 1);

			switch (pos) {
				case "nw":
					console.log("nw");
					polygon.attr({
						"points":
							[
								polygonPos.nw.x + dx, polygonPos.nw.y + dy,
								polygonPos.ne.x, polygonPos.ne.y + dy,
								polygonPos.se.x, polygonPos.se.y,
								polygonPos.sw.x + dx, polygonPos.sw.y
							]
					});

					circleArr[0].attr({
						cx: polygonPos.nw.x + dx,
						cy: polygonPos.nw.y + dy
					});
					circleArr[1].attr({
						cy: polygonPos.ne.y + dy
					});
					circleArr[3].attr({
						cx: polygonPos.sw.x + dx
					});

					break;
				case "ne":
					console.log("ne");
					polygon.attr({
						"points":
							[
								polygonPos.nw.x, polygonPos.nw.y + dy,
								polygonPos.ne.x + dx, polygonPos.ne.y + dy,
								polygonPos.se.x + dx, polygonPos.se.y,
								polygonPos.sw.x, polygonPos.sw.y
							]
					});

					circleArr[0].attr({
						cy: polygonPos.nw.y + dy
					});
					circleArr[1].attr({
						cx: polygonPos.ne.x + dx,
						cy: polygonPos.ne.y + dy
					});
					circleArr[2].attr({
						cx: polygonPos.se.x + dx
					});

					break;
				case"se":
					console.log("se");
					polygon.attr({
						"points":
							[
								polygonPos.nw.x, polygonPos.nw.y,
								polygonPos.ne.x + dx, polygonPos.ne.y,
								polygonPos.se.x + dx, polygonPos.se.y + dy,
								polygonPos.sw.x, polygonPos.sw.y + dy
							]
					});

					circleArr[1].attr({
						cx: polygonPos.ne.x + dx
					});
					circleArr[2].attr({
						cx: polygonPos.se.x + dx,
						cy: polygonPos.se.y + dy
					});
					circleArr[3].attr({
						cy: polygonPos.sw.y + dy
					});

					break;
				case"sw":
					console.log("sw");
					polygon.attr({
						"points":
							[
								polygonPos.nw.x + dx, polygonPos.nw.y,
								polygonPos.ne.x, polygonPos.ne.y,
								polygonPos.se.x, polygonPos.se.y + dy,
								polygonPos.sw.x + dx, polygonPos.sw.y + dy
							]
					});

					circleArr[0].attr({
						cx: polygonPos.nw.x + dx
					});
					circleArr[2].attr({
						cy: polygonPos.se.y + dy
					});
					circleArr[3].attr({
						cx: polygonPos.sw.x + dx,
						cy: polygonPos.sw.y + dy
					});

					break;
			}
		}

		function resize_Stop(e) {
			//Save Position
			polygonPos.nw.x = +polygon.attr("points")[0];
			polygonPos.nw.y = +polygon.attr("points")[1];
			polygonPos.ne.x = +polygon.attr("points")[2];
			polygonPos.ne.y = +polygon.attr("points")[3];
			polygonPos.se.x = +polygon.attr("points")[4];
			polygonPos.se.y = +polygon.attr("points")[5];
			polygonPos.sw.x = +polygon.attr("points")[6];
			polygonPos.sw.y = +polygon.attr("points")[7];
		}

		function draw_Polygon() {
			return snap.polygon().attr({
				points:
					[
						polygonPos.nw.x, polygonPos.nw.y,
						polygonPos.ne.x, polygonPos.ne.y,
						polygonPos.se.x, polygonPos.se.y,
						polygonPos.sw.x, polygonPos.sw.y
					]
			});
		}

		function draw_Start(e) {
			x_start = e.offsetX;
			y_start = e.offsetY;
			svgNode.addEventListener("mousemove", draw_Move);
		}

		function draw_Move(e) {
			let direction;

			x_end = e.offsetX;
			y_end = e.offsetY;

			direction = `${y_start < y_end ? "N" : "S"}${x_start < x_end ? "W" : "E"}`;

			snapToGrid();

			switch (direction) {
				case "NW":
					polygonPos.nw.x = x_start;
					polygonPos.nw.y = y_start;

					polygonPos.ne.x = x_end;
					polygonPos.ne.y = y_start;

					polygonPos.se.x = x_end;
					polygonPos.se.y = y_end;

					polygonPos.sw.x = x_start;
					polygonPos.sw.y = y_end;
					break;
				case "NE":
					polygonPos.nw.x = x_end;
					polygonPos.nw.y = y_start;

					polygonPos.ne.x = x_start;
					polygonPos.ne.y = y_start;

					polygonPos.se.x = x_start;
					polygonPos.se.y = y_end;

					polygonPos.sw.x = x_end;
					polygonPos.sw.y = y_end;
					break;
				case "SE":
					polygonPos.nw.x = x_end;
					polygonPos.nw.y = y_end;

					polygonPos.ne.x = x_start;
					polygonPos.ne.y = y_end;

					polygonPos.se.x = x_start;
					polygonPos.se.y = y_start;

					polygonPos.sw.x = x_end;
					polygonPos.sw.y = y_start;
					break;
				case "SW":
					polygonPos.nw.x = x_start;
					polygonPos.nw.y = y_end;

					polygonPos.ne.x = x_end;
					polygonPos.ne.y = y_end;

					polygonPos.se.x = x_end;
					polygonPos.se.y = y_start;

					polygonPos.sw.x = x_start;
					polygonPos.sw.y = y_start;
					break;
			}

			if (!polygon) {
				polygon = draw_Polygon();
			} else {
				polygon.attr(
					{
						"points": [
							polygonPos.nw.x, polygonPos.nw.y,
							polygonPos.ne.x, polygonPos.ne.y,
							polygonPos.se.x, polygonPos.se.y,
							polygonPos.sw.x, polygonPos.sw.y
						]
					})
			}

			polygon.attr({
				fill: colorPrimary,
				stroke: colorSecondary,
				"stroke-dasharray": "10,10",
				strokeWidth: 1,
				opacity: .5
			});
		}

		function draw_Stop() {

			//Remove draw handler
			svgNode.removeEventListener("mousedown", draw_Start);
			svgNode.removeEventListener("mousemove", draw_Move);
			svgNode.removeEventListener("mouseup", draw_Stop);
			svgNode.style.cursor = "default";

			polygon.attr({
				fill: colorPrimary,
				stroke: colorSecondary,
				"stroke-dasharray": "none",
				strokeWidth: 1,
				opacity: 1,
				class: element.type
			});


			//Draw handles
			let handle_NW = snap.circle(polygonPos.nw.x, polygonPos.nw.y, 5).attr({
					fill: colorSecondary,
					cursor: "nwse-resize",
					"data-pos": "nw"
				}),
				handle_NE = snap.circle(polygonPos.ne.x, polygonPos.ne.y, 5).attr({
					fill: colorSecondary,
					cursor: "nesw-resize",
					"data-pos": "ne"
				}),
				handle_SE = snap.circle(polygonPos.se.x, polygonPos.se.y, 5).attr({
					fill: colorSecondary,
					cursor: "nwse-resize",
					"data-pos": "se"
				}),
				handle_SW = snap.circle(polygonPos.sw.x, polygonPos.sw.y, 5).attr({
					fill: colorSecondary,
					cursor: "nesw-resize",
					"data-pos": "sw"
				});


			//Add drag handler
			polygon.drag(drag_Move, drag_Start, drag_Stop);
			circleArr.push(handle_NW, handle_NE, handle_SE, handle_SW);


			//Add resize handler
			circleArr.forEach(handle => handle.drag(resize_Move, resize_Start, resize_Stop))

		}

		svgNode.addEventListener("mousedown", draw_Start);	//svgNode = area
		svgNode.addEventListener("mouseup", draw_Stop);
		svgNode.style.cursor = "crosshair";
	};

	componentDidMount() {
		this.svgRender();
	}

	componentDidUpdate() {
		this.svgRender();
	}

	componentWillUnmount() {
		//ToDo delete svg
	}

	render() {

		return (
			<React.Fragment>
				<h1>Konzept fr das dynamische Erstellen von Seitenlayouts</h1>
				<p>WIP</p>
				<div className="svgGridSidebar">
					<Button {...this.buttons.header}/>
					<Button {...this.buttons.sidebar}/>
					<Button {...this.buttons.content}/>
					<Button {...this.buttons.clear}/>
					<Select {...this.select}/>
					<InputNumber {...this.input}/>
					<Button {...this.buttons.export}/>
				</div>

				<svg className="gridWrapper" ref={d => this.svgDiv = d}/>

			</React.Fragment>
		)
	}
}

SVGGrid.contextType = AppContext;

export default SVGGrid;
