import React, {Component} from 'react';
import AppContext from '../../../../App-context';
import {Transition} from 'react-spring/renderprops';
import emitter from'../../../../helpers';

import githubImage from '../../../../images/GitHub-Mark-Light-120px-plus.png'
import './CallToAction.scss';

let Subscription = null;

class CallToAction extends Component {


	componentDidMount() {
		let vitaEntry = this.vitaEntry;
		Subscription = emitter.addListener('pocScroll', data => {
			let paragraph = +vitaEntry.dataset.paragraph;

			if (paragraph === data) {
				window.scrollTo({
					top: vitaEntry.offsetTop - 50
				})
			}
		});
	}

	componentWillUnmount() {
		Subscription.remove();
	}

	render() {
		return (
			<AppContext.Consumer>
				{({appState}) => {
					let data = appState.pocs[appState.currentPOC],
						show = data.show;

					return (
						<Transition
							items={show}
							from={{right: "-100%"}}
							enter={{right: "0%"}}
							leave={{right: "-100%"}}>
							{(show) =>
								show && (props => {
									return (
										<div style={props} className="call-to-action">
											<img src = {githubImage}/>
											<p>View on Github</p>
											<a href={data.url} target="_blank"/>
										</div>
									)
								})
							}
						</Transition>
					)
				}}
			</AppContext.Consumer>
		);
	}
}

export default CallToAction;
