import React, {Component} from 'react';
import AppContext from "../../../App-context";

import VitaEntryContent from "./VitaEntryContent";
import {isInViewport} from "../../../variables";

import emitter from "../../../helpers";

let Subscription = null;

class VitaEntry extends Component {

	constructor(props) {
		super(props);
		this.setParagraph = this.setParagraph.bind(this);
	}

	setParagraph() {
		let paragraph = +this.vitaEntry.dataset.paragraph;
		if (isInViewport(this.vitaEntry)) {
			this.context.setCurrentParagraph(paragraph)
		}
	}

	scrollTo(data) {
		let paragraph = +this.vitaEntry.dataset.paragraph;
		if (paragraph === data) {
			window.scrollTo({
				top: this.vitaEntry.offsetTop - 50
			})
		}
	}

	componentDidMount() {
		let vitaEntry = this.vitaEntry;

		Subscription = emitter.addListener('paragraphScroll', data => {
			let paragraph = +vitaEntry.dataset.paragraph;

			if (paragraph === data) {
				window.scrollTo({
					top: vitaEntry.offsetTop - 50
				})
			}
		});
		window.addEventListener("scroll", this.setParagraph);
	}

	componentWillUnmount() {
		Subscription.remove();
		window.removeEventListener("scroll", this.setParagraph);
	}

	render() {
		const {id, content, title} = this.props.item;

		return (
			<div key={id} className="paragraph">
				<h2 data-paragraph={id} ref={el => this.vitaEntry = el}>{title}</h2>
				{content.map(item => <VitaEntryContent key={item.id} item={item}/>)}
			</div>
		);
	}
}

VitaEntry.contextType = AppContext;

export default VitaEntry;