import React, {Component} from 'react';
import './Playground.scss';

import SVGGrid from './SVGGrid/SVGGrid';
import CallToAction from './CallToAction/CallToAction';
import emitter from "../../../helpers";
import {isInViewport} from "../../../variables";


class Playground extends Component {

	emitScrollEvent = id => {
		emitter.emit('pocScroll', id);
	};

	componentDidMount() {
		window.addEventListener("scroll", this.setPOC);
	}

	componentWillUnmount() {
		window.removeEventListener("scroll", this.setPOC);
	}

	render() {
		//const {id, content, title, year} = this.props.item;
		return (
			<div className="playground">
				<SVGGrid/>
				<CallToAction/>
			</div>
		);
	}
}

export default Playground;