import React, {Component} from 'react';
import AppContext from '../../../App-context';
import './Vita.scss';

import VitaEntry from './VitaEntry';

class Vita extends Component {

	render() {
		return (
			<div className="vita">
				<AppContext.Consumer>
					{context => context.appState.vita.map(item => <VitaEntry key={item.id} item={item}/>)}
				</AppContext.Consumer>
			</div>
		)
	}
}

export default Vita;
