import React, {Component} from 'react';
import {Transition} from 'react-spring/renderprops';

import './Select.scss';
import {COLORS} from "../../../variables";

class Select extends Component {

	constructor() {
		super();
		this.state = {
			activeOption: null,
			show: false
		};
	}

	toggleDropdown = () => {
		this.setState(state => ({show: !this.state.show}));
		//this.vitaEntry
	};

	updateSelectValue = (label, id, callback) => {
		this.toggleDropdown();
		this.setState(state => ({activeOption: label}));
		callback(id);
	};

//ToDO remove select grid size
	render() {
		const {callback, options, style} = this.props;

		return (
			<div className="dropdownWrapper">
				<div className="dropdown">
					<div className="option active"
						 onClick={this.toggleDropdown}>{this.state.activeOption ? `${this.state.activeOption}` : "Select Grid Size"}</div>

					<Transition
						items={this.state.show}
						from={{opacity: 0}}
						enter={{opacity: 1}}
						leave={{opacity: 0}}>
						{(show) =>
							show && (props => {
								return (
									options.map(item =>
										<div className="option"
											 key={item.id}
											 onClick={() => this.updateSelectValue(item.label, item.value, callback)}
											 ref={el => this.option = el}
											 style={props}>{item.label}</div>
									)
								)
							})
						}
					</Transition>
				</div>
			</div>
		);
	}
}

export default Select;
