import React, {Component} from 'react';
import AppContext from '../../App-context';
import {Transition} from 'react-spring/renderprops';
import emitter from'../../helpers';

import './Sidebar.scss';


class Sidebar extends Component {

	emitScrollEvent = (id, setCurrentParagraph) => {
		setCurrentParagraph(id);
		emitter.emit('paragraphScroll', id);
	};

	render() {
		return (
			<AppContext.Consumer>
				{({appState, setCurrentParagraph}) => {
					let show = appState.currentPage === 2;
					return (
						<Transition
							items={show}
							from={{bottom: "-100%"}}
							enter={{bottom: "0%"}}
							leave={{bottom: "-100%"}}>
							{(show) =>
								show && (props => {
									return (
										<div style={props} className="sidebar">
											<hr/>
											<ul>{appState.vita.map(item => <li
												onClick={() => this.emitScrollEvent(item.id, setCurrentParagraph)}
												className={item.id === appState.currentParagraph ? "active" : ""}
												key={item.id}>{item.title}</li>)}</ul>
										</div>
									)
								})
							}
						</Transition>
					)
				}}
			</AppContext.Consumer>
		);
	}
}

export default Sidebar;
