import React, {Component} from 'react';
import AppContext from '../../App-context';
import './Content.scss';

import About from './About/About';
import Vita from './Vita/Vita';
import Playground from './Playground/Playground';
import {Transition} from "react-spring/renderprops";

class Content extends Component {

	render() {
		const pages = {
			1: {
				component: <About/>,
				class: "content-about"
			},
			2: {
				component: <Vita/>,
				class: "content-vita"
			},
			3: {
				component: <Playground/>,
				class: "content-playground"
			}
		};

		return (
			<div className="content">
				<AppContext.Consumer>
					{context => {
						let toggle = context.appState.currentPage;
						return (
							<Transition
								items={toggle}
								from={{opacity: 0}}
								enter={{opacity: 1}}
								leave={{display: "none"}}>
								{toggle => props => <div className={pages[toggle].class} style={props}>{pages[toggle].component}</div>}
							</Transition>
						)
					}}
				</AppContext.Consumer>
			</div>
		);
	}


	/*
		render() {
			//const {id, content, title, year} = this.props.item;
			return (
				<div className="content">
					<AppContext.Consumer>
						{context => {
							if (context.appState.currentPage === 1) {
								return (
									<About/>
								)
							} else if (context.appState.currentPage === 2) {
								return (
									<Vita/>
								)
							} else if (context.appState.currentPage === 3) {
								return (
									<Playground/>
								)
							}
						}}
					</AppContext.Consumer>
				</div>
			);
		}*/
}

export default Content;
