import React, {Component} from 'react';
import {Transition} from 'react-spring/renderprops';

import './Input.scss';

class InputNumber extends Component {

	constructor() {
		super();
		this.state = {
			inputValue: 10
		};
	}

	updateInputValue(id, callback) {
		this.setState({
			inputValue: id
		});
		callback(id);
	}

	render() {

		const {style: {colorPrimary, colorSecondary}, callback, attributes} = this.props;
		let style = {
			"borderColor": colorSecondary,
			"color": colorSecondary,
			"background": colorPrimary
		};

		return (
			<div className="input-number" style={style}>
				<div className="grid-substract"
					 onClick={e => this.updateInputValue(+this.state.inputValue - 1, callback)}>-
				</div>
				<input {...attributes} className="grid-input"
					   onChange={e => this.updateInputValue(e.target.value, callback)}/>
				<div className="grid-add"
					 onClick={e => this.updateInputValue(+this.state.inputValue + 1, callback)}>+
				</div>
			</div>
		);
	}
}

export default InputNumber;
