import React, {Component} from 'react';
import AppContext from '../../App-context';
import './Header.scss';

class Header extends Component {

	setPage = (id, setCurrentPage) => {
		setCurrentPage(id);
	};

	render() {
		return (
			<AppContext.Consumer>
				{({appState, setCurrentPage}) => (
					<div className="header">
						<ul>{appState.paragraphs.map(item => <li key={item.id} className={item.id === appState.currentPage ? "active" : ""} onClick={() => this.setPage(item.id, setCurrentPage)}>{item.title}</li>)}</ul>
						<div className="langSelector"></div>
					</div>
				)}
			</AppContext.Consumer>
		);
	}
}

export default Header;
