import React, {Component} from 'react';
import './About.scss';

import AppContext from "../../../App-context";
import {Transition} from "react-spring/renderprops";

class About extends Component {


	render() {

		return (
			<div className="about">
				<AppContext.Consumer>
					{({appState}) => {
						let show = appState.currentPage === 1;
						return (
							<React.Fragment>
								<div className="about-header">
									<Transition
										items={show}
										from={{left: "-100%"}}
										enter={{left: "0%"}}
										leave={{left: "-100%"}}>
										{(show) =>
											show && (props => {
												return (
													<div style={props} className="curtain-left">
														<p>Hi. Mein Name ist</p>
														<p>Und ich bin</p>
													</div>
												)
											})
										}
									</Transition>
									<Transition
										items={show}
										from={{right: "-100%"}}
										enter={{right: "0%"}}
										leave={{right: "-100%"}}>
										{(show) =>
											show && (props => {
												return (
													<div style={props} className="curtain-right">
														<p>Moritz.</p>
														<p>Web-Entwickler.</p>
													</div>
												)
											})
										}
									</Transition>
								</div>
								<div className="about-content">
									{appState.about.content.map((item, i) => <p key={i}>{item}</p>)}
								</div>
							</React.Fragment>
						)
					}}

				</AppContext.Consumer>
			</div>
		);
	}
}

export default About;
