import React, {Component} from 'react';
import AppContext from '../../../App-context';
import {Transition} from 'react-spring/renderprops';

import './Button.scss';

class Button extends Component {

	render() {

		const {label = "", style: {border, colorPrimary, colorSecondary}, callback} = this.props;

		let style = {
			"borderColor": border ? colorSecondary : colorPrimary,
			"color": colorSecondary,
			"background": colorPrimary
		};

		return (
			<button style={style} className="button" onClick={callback}>{label}</button>
		);
	}
}

export default Button;
